import React, { useMemo } from "react";
import { Modal } from "react-bootstrap";
import { ARButton } from "./components/ARButton/ARButton";
import { BuyNowButton } from "./components/BuyNowButton/BuyNowButton";
import { ContactUsButton } from "./components/ContactUsButton/ContactUsButton";
import { Excerpt } from "./components/Excerpt/Excerpt";
import { ImageCarousel } from "./components/ImageCarousel/ImageCarousel";
import { IndicationComponent } from "./components/IndicationComponent/IndicationComponent";
import { KeySpec } from "./components/KeySpec/KeySpec";
import { PdfButton } from "./components/PdfButton/PdfButton";
import { ProductDetailsButton } from "./components/ProductDetailsButton/ProductDetailsButton";
import { ProductTitleComponent } from "./components/ProductTitle/ProductTitle";
import { QRAR } from "./components/QRAR/QRAR";
import { Switch } from "./components/Switch/Switch";
import { VariantProduct } from "./components/VariantProduct/VariantProduct";
import { VideoButton } from "./components/VideoButton/VideoButton";
import styles from "./ProductModal.module.scss";

const ProductModal = ({
  openVideoModal,
  handleClose,
  setVariant,
  selectedProduct,
  variationOptions,
  openPdfModal,
  closePdfModal,
  openPdf,
}) => {
  return useMemo(() => {
    if (!selectedProduct) return null;
    const { Model, Indication, HasAR, VideoLink, ContactUs, PdfPath, BuyNow, ARLink } = selectedProduct;
    console.log('variation', variationOptions)
    
    return (
      <Modal backdrop={false} show={Boolean(selectedProduct)} onHide={handleClose}>
        <div className={styles.container}>
          <div className={styles.mainContent}>
          <Switch />
            <ImageCarousel product={selectedProduct} />
            <div className={styles.titleAndKeySpecs}>
              <ProductTitleComponent setVariant={setVariant} product={selectedProduct} variationOptions={variationOptions} />
              <KeySpec product={selectedProduct} />
            </div>
            <div className={styles.btnContainer}>
              <ProductDetailsButton product={selectedProduct} />
              {PdfPath && <PdfButton product={selectedProduct} openPdfModal={openPdfModal} />}
              {ContactUs && <ContactUsButton product={selectedProduct} Model={Model} />}
              {VideoLink && <VideoButton openVideoModal={openVideoModal} Model={Model} />}
              {BuyNow && <BuyNowButton product={selectedProduct} Model={Model} />}
              {ARLink && <ARButton product={selectedProduct} Model={Model} />}
            </div>
          </div>
          <div className={styles.productDetailsColumn}>
            <div className={styles.productContent}>
              <p className={styles.recProductHeading}>You may also like</p>
              {variationOptions && <VariantProduct variantOptions={variationOptions}/>}
            </div>
          </div>
        </div>
      </Modal>
    );
  }, [selectedProduct, handleClose, setVariant, variationOptions, openVideoModal, openPdfModal]);
};

export { ProductModal };
