import React from "react";
import styles from "./VariantProduct.module.scss";

const VariantProduct = ({ variantOptions }) => {

    const IMAGES = require.context("../../../../../public/product-images", true, /\.jpg$/);

    return (
        <>
            {variantOptions.map((variant) => (
                <div className={styles.variantContainer}>
                    <img className={styles.variantImage} src={IMAGES.keys()
                    .filter((file) => file.startsWith(`./${variant.ImagePath}`))
                    .map((filePath) => `${process.env.PUBLIC_URL}/product-images${filePath.substring(1)}`)[0]} alt={variant.ProductTitle}/>
                    <div className={styles.variantDetails}>
                        <p className={styles.variantTitle}>{variant.ProductTitle} {variant.Model}</p>
                        <p className={styles.variantKeySpecs}>{variant.KeySpecs}</p>
                        <button className={styles.btn}>See More</button>
                    </div>
                </div>
            ))}
        </>
    )
}

export { VariantProduct };