import React, { useState } from "react";
import styles from "./Switch.module.scss";

const Switch = () => {

    const [ twoDActive, setTwoDActive ] = useState(true)
    const [ threeDActive, setThreeDActive ] = useState(false)

    const toggleClick = () => {
        setTwoDActive(!twoDActive)
        setThreeDActive(!threeDActive)
        console.log('2d', twoDActive)
        console.log('3d', threeDActive)
    }

    return (
        <div className={styles.switchContainer}>
            <div id={styles.twoDButton} className={twoDActive ? styles.btnActive : styles.btnInactive} onClick={toggleClick}>2D</div>
            <div id={styles.threeDButton} className={threeDActive ? styles.btnActive : styles.btnInactive} onClick={toggleClick}>3D</div>
        </div>
    )
}

export { Switch };