import React, { useMemo, useState } from "react";
import styles from "./ProductTitle.module.scss";

const ProductTitleComponent = ({ setVariant, product, variationOptions }) => {
  const { Model, ProductTitle } = product;
  const [showDropdown, setShowDropdown] = useState(false);

  return useMemo(() => {
    const toggleVariant = () => {
      setVariant(null);
      setShowDropdown(!showDropdown);
    };

    const getModelText = () => {
      const words = Model.split('/')
      if (words.length >= 2) {
        return words.join(' / ')
      } else {
        return words.join('')
      }
    }
    return (
      <div>
        <div onClick={toggleVariant}>
          <p className={styles.productModel}>
          {ProductTitle} - {getModelText()}
          </p>
        </div>
      </div>
    );
  }, [Model, variationOptions, ProductTitle, showDropdown, setVariant]);
};

export { ProductTitleComponent };
