import React, { useEffect, useState } from "react";
import { PdfModal } from "../ProductModal/components/PdfModal/PdfModal";
import { ProductModal } from "../ProductModal/ProductModal";
import { VideoModal } from "../VideoModal/VideoModal";

const PRODUCTS = require("../../products.json");
const VARIATIONS = require("../../variations.json");

const findProduct = (jsonList, model, country) => {
  return jsonList.find((product) => product.Model === model && product.Site === country);
};

function ModalContainerComponent({ vistaContent }) {
  const [variant, setVariant] = useState(null);
  const [product, setProduct] = useState(null);

  console.log(findProduct(VARIATIONS, product?.Model, product?.Site)?.Variations)

  function toggleVistaAudio(state) {
    const audios = vistaContent.current.contentWindow.currentGlobalAudios;
    for (let audio in audios) {
      if (state === "ON") {
        audios[audio].audio.play();
      }
      if (state === "OFF") {
        audios[audio].audio.pause();
      }
    }
  }

  const [openVideo, setOpenVideo] = useState(false);
  function openVideoModal() {
    setOpenVideo(true);
    toggleVistaAudio("OFF");
  }
  function closeVideoModal() {
    setOpenVideo(false);
    toggleVistaAudio("ON");
  }

  const handleClose = () => {
    setVariant(null);
    setProduct(null);
    setOpenVideo(false);
    toggleVistaAudio("ON");
  };

  // PDF
  const [openPdf, setOpenPdf] = useState(false);
  function openPdfModal() {
    setOpenPdf(true);
  }

  function closePdfModal() {
    setOpenPdf(false);
  }

  useEffect(() => {
    function callback(e) {
      // if (e.data.model) {
        // const targetProduct = findProduct(PRODUCTS, e.data.model, e.data.country);
        const targetProduct = {
          "No.": null,
          "Model": "RZ-S500",
          "Site": "Thailand",
          "Colour": "Black / White",
          "Work Type-Option 1": "Very Simple Modification",
          "Work Type-Option 2": "Simple Modification",
          "Product name": "{\\title(Module specific)\\\":\\\"Yes\\\"}\"",
          "Product SKU (full SMN)(Country specific)": "Yes",
          "Gallery image(Model specific)": "Yes",
          "Gallery Video(Country specific)": "Yes",
          "Spec Image(If no gallery image, provide spec image)": "No",
          "key feature(Module specific)": "Yes",
          "Version": "V3",
          "Link": "https://www.panasonic.com/my/consumer/home-entertainment/headphone/true-wireless-earbuds/rz-s500we.html?utm_source=virtualexperience.apac.panasonic.com&utm_medium=referral&utm_campaign=space_solution&utm_content=study",
          "key spec 1": "High-Performance Dual Hybrid Noise Cancelling",
          "Indication": "MY : RS-S500WE\nSG : RS-S500W\nID   : RS-S500W\nPH : RS-S500",
          "smn": "RZ-S500WE",
          "ProductTitle": "True Wireless Noise Cancelling Earbuds",
          "VideoLink": "https://www.youtube.com/embed/p7VO4GeCPZI",
          "ImagePath": "test/RZ-S500",
          "APText": "Tune in to the sound that relaxes your mind",
          "APIcons": "icon/Enrich.svg",
          "BuyNow": "https://www.google.com",
          "ARLink": "www.google.com",
          "PdfPath": "Analog Smoke Detector",
          "ContactUs": "https://www.google.com",
          "Javascript": "window.parent.postMessage({model: 'RZ-S500', country: ‘Thailand’})",
          "HasAR": false,
        }
        setProduct(targetProduct);
        console.log(e.data.model)
        console.log(targetProduct)
      // }
    }
    window.addEventListener("message", callback);

    return () => {
      window.removeEventListener("message", callback);
    };
  }, []);

  return (
    <>
      {openVideo && <VideoModal closeVideoModal={closeVideoModal} selectedProduct={variant || product} />}
      {openPdf && <PdfModal closePdfModal={closePdfModal} selectedProduct={variant || product} />}
      <ProductModal
        handleClose={handleClose}
        openVideoModal={openVideoModal}
        selectedProduct={variant || product}
        setVariant={setVariant}
        variationOptions={findProduct(VARIATIONS, product?.Model, product?.Site)?.Variations || []}
        openPdfModal={openPdfModal}
        closePdfModal={closePdfModal}
        openPdf={openPdf}
      />
    </>
  );
}

const ModalContainer = React.memo(ModalContainerComponent);

export { ModalContainer, findProduct };
